const Spinner = () => (
  <div
    style={{
      display: "flex",
      height: 400,
      justifyContent: "center",
      alignItems: "center",
    }}
  >
    <svg className="spinner" viewBox="0 0 50 50">
      <circle
        className="path"
        cx="25"
        cy="25"
        r="20"
        fill="none"
        strokeWidth="5"
      ></circle>
    </svg>
  </div>
);

export default Spinner;
